// Main javascript entry point
// Should handle bootstrapping/starting application

'use strict';

import $ from 'jquery';
import 'lazyload';
import 'TweenLite';
import 'EasePack';
import 'AttrPlugin';
import 'CSSPlugin';
import 'doT';

import './common/_modernizr';

import MaterialDesign from './common/_material-design';

// import Header from '../_modules/header/header';
// import Navigation from '../_modules/navigation/navigation';
import Galisteners from '../_modules/galisteners/galisteners';
import Slick from '../_modules/slick/slick';
import Form from '../_modules/form/form';
import ServiceWorker from '../_modules/serviceworker/serviceworker';
import { isMobile } from './common/_util';


$(() => {
    // new Header();
    // new Navigation();
    new Slick();
    new Form();

    // Init Material Design
    const material = new MaterialDesign();
    material.init();


    // Init Google Analytics
    const ga = new Galisteners();
    ga.init();


    // Set framerate to 60fps
    TweenLite.ticker.fps(60);


    // Init Lazy Loading
    $('.lazy').lazyload({
        effect: 'fadeIn',
        placeholder: 'data:image/png;base64,'
    });


    // JavaScript hack for opening links into new windows if target="_blank" is not set
    let a = new RegExp('/' + window.location.host + '/');
    $('a').click(function () {
        if (!a.test(this.href)) {
            window.open($(this).attr('href'));
            return false;
        }
    });

    $('.-js-explore').on('click', function () {
        $('html, body').animate({
            scrollTop: isMobile() == true ? $('.section__services').offset().top - $('.header').outerHeight() : $('.section__services').offset().top
        }, 750);
    });


    // Warning alert for leaving the page
    // window.onbeforeunload = function (e) {
    //     e = e || window.event;

    //     // For IE and Firefox prior to version 4
    //     if (e) {
    //         e.returnValue = 'Any string';
    //     }

    //     // For Safari
    //     return 'Any string';
    // };


    // Simple Service Worker to make App Install work (OPTIONAL)
    // new ServiceWorker();


    // material.toaster("I'm a wayv-digital!!!", 0);
    console.log("I'm a wayv-digital!!!");
});
